<template>
		<div id="WebGL-output">
		</div>
</template>

<script>
	//import imgPath_posx from '@/assets/images/three'

		import imgPath_posx from "@/assets/images/three/posx.jpg";
		import imgPath_negx from"@/assets/images/three/negx.jpg";
		import imgPath_posy from"@/assets/images/three/posy.jpg";
		import imgPath_negy from"@/assets/images/three/negy.jpg";
		import imgPath_posz from"@/assets/images/three/posz.jpg";
		import imgPath_negz from"@/assets/images/three/negz.jpg";
		// import "@/assets/js/TrackballControls.js"

	export default {
		data(){
			return{
				renderer:{},
				planeGeometry:{},
				scene:{},
				camera:{},
				textureCube_sky:{},
				// imgPath:'https://threejs.org/examples/textures/cube/Bridge2/'
				imgPathUrl:[
					imgPath_posx,imgPath_negx,
					imgPath_posy,imgPath_negy,
					imgPath_posz,imgPath_negz
				],
				cameraControls:{}

			}
		},//data()
		mounted(){
			this.initWEBGL();
		},//mounted
		methods:{
			initWEBGL(){
				// SCENE
				this.textureCube_sky = new THREE.CubeTextureLoader()
					//.setPath(imgPath)
					//.load( [ 'posx.jpg', 'negx.jpg', 'posy.jpg', 'negy.jpg', 'posz.jpg', 'negz.jpg' ] );
					//.load([	imgPath_posx,imgPath_negx,imgPath_negx,imgPath_negy,imgPath_posz,imgPath_negz]);
					.load(this.imgPathUrl);
					
				this.scene = new THREE.Scene();
				this.scene.background = this.textureCube_sky;
				
				this.camera = new THREE.PerspectiveCamera(
						50,
						window.innerWidth / window.innerHeight,
						0.1,1000
				);

				this.camera.position.x = -20;
				this.camera.position.y = 60;
				this.camera.position.z = 50;
				this.camera.lookAt(this.scene.position);

				this.scene.fog = new THREE.Fog(0xffffff,0.005,100);
				this.renderer = new THREE.WebGLRenderer();
				this.renderer.setClearColor( new THREE.Color(0xefefef,0.5) );
			    this.renderer.setSize(window.innerWidth,window.innerHeight);
				this.renderer.shadowMapEnabled = true;
				
				var ambientLight = new THREE.AmbientLight(0x0c0c0c);
				this.scene.add(ambientLight);

				var spotLight = new THREE.SpotLight(0xffffff);
				spotLight.position.x = -40;
				spotLight.position.y = 60;
				spotLight.position.z = -10;

				this.scene.add(spotLight);
				
				this.cameraControls = new THREE.TrackballControls(this.camera);

				//document.body.appendChild(renderer.domElement);
				document.getElementById("WebGL-output").append(this.renderer.domElement);
				this.render();

			},//initWEBGL
			render(){

					this.cameraControls.update();
				       //this.addCube();
				       this.renderer.render(this.scene,this.camera);
				       requestAnimationFrame(this.render);
			},//render()
		},//methods
	}
</script>


